.profile-container {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  text-align: center;
}

.profile-container h2 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.profile-container p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  color: #666;
}

.profile-options {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}

.profile-option {
  display: block;
  padding: 1rem;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  text-decoration: none;
  color: #333;
  font-weight: 500;
  transition: all 0.3s ease;
}

.profile-option:hover {
  background-color: #f8f8f8;
  border-color: #999;
}

.btn-logout {
  background-color: #000;
  color: #fff;
  border: none;
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.btn-logout:hover {
  background-color: #333;
}

/* Estilos específicos para tablas en páginas de administración */
.admin-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
}

.admin-table th,
.admin-table td {
  padding: 0.75rem;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.admin-table th {
  background-color: #f8f8f8;
  font-weight: 600;
}

.admin-table tr:hover {
  background-color: #f5f5f5;
}

.btn-edit {
  background-color: #f0ad4e;
  color: white;
  border: none;
  padding: 0.3rem 0.6rem;
  border-radius: 3px;
  cursor: pointer;
}

.btn-delete {
  background-color: #d9534f;
  color: white;
  border: none;
  padding: 0.3rem 0.6rem;
  border-radius: 3px;
  cursor: pointer;
  margin-left: 0.5rem;
}

/* Estilos para estados de pedidos */
.status-pending {
  color: #f0ad4e;
}

.status-completed {
  color: #5cb85c;
}

.status-cancelled {
  color: #d9534f;
}