/* Main CSS file for the Event Organizer application */

:root {
  --primary-color: #6c5ce7;
  --secondary-color: #a29bfe;
  --accent-color: #fd79a8;
  --text-color: #2d3436;
  --light-color: #f5f6fa;
  --dark-color: #2d3436;
  --success-color: #00b894;
  --warning-color: #fdcb6e;
  --danger-color: #d63031;
  --border-radius: 8px;
  --box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  --transition: all 0.3s ease;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
  color: var(--text-color);
  background-color: var(--light-color);
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Header Styles */
.header {
  background-color: white;
  box-shadow: var(--box-shadow);
  padding: 1rem 0;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--primary-color);
  text-decoration: none;
}

.nav-menu {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-item {
  margin-left: 1.5rem;
}

.nav-link {
  color: var(--text-color);
  text-decoration: none;
  transition: var(--transition);
}

.nav-link:hover {
  color: var(--primary-color);
}

/* Hero Section */
.hero {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('../assets/images/hero-bg.jpg');
  background-size: cover;
  background-position: center;
  color: white;
  padding: 5rem 0;
  text-align: left;
}

.hero-content {
  max-width: 600px;
}

.hero-title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.hero-text {
  font-size: 1.1rem;
  margin-bottom: 2rem;
}

.btn {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  border-radius: var(--border-radius);
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;
  transition: var(--transition);
}

.btn-primary {
  background-color: var(--primary-color);
  color: white;
  border: none;
}

.btn-primary:hover {
  background-color: var(--secondary-color);
  transform: translateY(-3px);
}

/* Featured Products Section */
.featured-products {
  padding: 4rem 0;
}

.section-title {
  text-align: center;
  margin-bottom: 3rem;
  font-size: 2rem;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  padding: 2rem;
}

.product-card {
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  overflow: hidden;
  transition: var(--transition);
  display: flex;
  flex-direction: column;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.product-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.product-content {
  padding: 1.5rem;
}

.product-title {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.product-description {
  color: #666;
  margin-bottom: 1rem;
}

.product-price {
  font-weight: bold;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

/* Values Section */
.values-section {
  padding: 4rem 0;
  background-color: #f9f9f9;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
}

.value-card {
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 2rem;
  text-align: center;
}

.value-icon {
  font-size: 2.5rem;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

.value-title {
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

/* Gallery Section */
.gallery-section {
  padding: 4rem 0;
}

.gallery-title {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 2rem;
}

.gallery-subtitle {
  text-align: center;
  margin-bottom: 3rem;
  color: #666;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1rem;
  justify-items: center;
  align-items: center;
}

.gallery-item {
  border-radius: var(--border-radius);
  overflow: hidden;
  height: 250px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gallery-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition);
}

.gallery-img:hover {
  transform: scale(1.05);
}

/* Testimonials Section */
.testimonials-section {
  padding: 4rem 0;
  background-color: #f9f9f9;
}

.testimonials-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
}

.testimonial-card {
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 2rem;
}

.testimonial-text {
  font-style: italic;
  margin-bottom: 1.5rem;
}

.testimonial-author {
  display: flex;
  align-items: center;
}

.author-img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 1rem;
}

.author-name {
  font-weight: bold;
}

/* CTA Section */
.cta-section {
  padding: 4rem 0;
  text-align: center;
}

.cta-btn {
  background-color: var(--primary-color);
  color: white;
  padding: 1rem 2rem;
  border-radius: var(--border-radius);
  font-size: 1.1rem;
  border: none;
  cursor: pointer;
  transition: var(--transition);
}

.cta-btn:hover {
  background-color: var(--secondary-color);
  transform: translateY(-3px);
}

/* Footer */
.footer {
  background-color: var(--dark-color);
  color: white;
  padding: 3rem 0;
}

.footer-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
}

.footer-logo {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-link {
  margin-bottom: 0.5rem;
}

.footer-link a {
  color: #ddd;
  text-decoration: none;
  transition: var(--transition);
}

.footer-link a:hover {
  color: white;
}

.social-icons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.social-icon {
  color: white;
  font-size: 1.5rem;
  transition: var(--transition);
}

.social-icon:hover {
  color: var(--primary-color);
}

.copyright {
  text-align: center;
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

/* Checkout Styles */
.checkout-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.checkout-title {
  font-size: 2rem;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.cart-item {
  display: flex;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid #eee;
  gap: 1rem;
}

.cart-item-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: var(--border-radius);
}

.cart-item-details {
  flex-grow: 1;
}

.cart-item-title {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.cart-item-price {
  color: var(--primary-color);
  font-weight: bold;
}

.cart-item-actions {
  display: flex;
  gap: 1rem;
}

.promo-code {
  margin: 2rem 0;
  padding: 1rem;
  border: 1px solid #eee;
  border-radius: var(--border-radius);
}

.promo-code-input {
  display: flex;
  gap: 1rem;
}

.promo-code input {
  flex-grow: 1;
  padding: 0.5rem;
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
}

.promo-code button {
  padding: 0.5rem 1rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: var(--border-radius);
  cursor: pointer;
}

.checkout-summary {
  margin-top: 2rem;
  padding: 1rem;
  background-color: #f8f9fa;
  border-radius: var(--border-radius);
}

.summary-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.summary-total {
  font-weight: bold;
  border-top: 2px solid #ddd;
  padding-top: 1rem;
  margin-top: 1rem;
}

.checkout-button {
  width: 100%;
  padding: 1rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: var(--border-radius);
  cursor: pointer;
  margin-top: 1rem;
  font-weight: bold;
}

.checkout-button:hover {
  background-color: var(--secondary-color);
}

/* Responsive Styles */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2rem;
  }
  
  .products-grid,
  .values-grid,
  .gallery-grid,
  .testimonials-grid,
  .footer-container {
    grid-template-columns: 1fr;
  }
}