/* Estilos para los componentes de administración */

.admin-container {
  padding: 2rem;
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin: 2rem auto;
  max-width: 1000px;
}

.admin-container h2 {
  color: var(--primary-color);
  margin-bottom: 1.5rem;
  border-bottom: 2px solid var(--secondary-color);
  padding-bottom: 0.5rem;
}

.admin-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
}

.admin-table th,
.admin-table td {
  padding: 0.75rem;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.admin-table th {
  background-color: var(--secondary-color);
  color: white;
  font-weight: 500;
}

.admin-table tr:hover {
  background-color: #f5f5f5;
}

.btn-edit,
.btn-delete,
.btn-view,
.btn-reply,
.btn-add {
  padding: 0.5rem 0.75rem;
  border: none;
  border-radius: var(--border-radius);
  cursor: pointer;
  margin-right: 0.5rem;
  font-size: 0.9rem;
  transition: var(--transition);
}

.btn-edit {
  background-color: var(--warning-color);
  color: var(--dark-color);
}

.btn-delete {
  background-color: var(--danger-color);
  color: white;
}

.btn-view {
  background-color: var(--primary-color);
  color: white;
}

.btn-reply {
  background-color: var(--success-color);
  color: white;
}

.btn-add {
  background-color: var(--success-color);
  color: white;
  padding: 0.75rem 1rem;
  margin-bottom: 1rem;
  font-weight: 500;
  display: inline-block;
}

.btn-edit:hover,
.btn-delete:hover,
.btn-view:hover,
.btn-reply:hover,
.btn-add:hover {
  opacity: 0.9;
  transform: translateY(-2px);
}

/* Estilos para selects en tablas */
.admin-table select {
  padding: 0.5rem;
  border-radius: var(--border-radius);
  border: 1px solid #ddd;
  background-color: white;
  width: 100%;
}

/* Responsive */
@media (max-width: 768px) {
  .admin-container {
    padding: 1rem;
  }
  
  .admin-table {
    display: block;
    overflow-x: auto;
  }
  
  .btn-edit,
  .btn-delete,
  .btn-view,
  .btn-reply {
    padding: 0.4rem 0.6rem;
    font-size: 0.8rem;
  }
}